'use client';

import { useEffect, useState } from 'react';
import { useLanguageStore } from '@/stores/languageStore';
import { useTranslationReady } from '@/hooks/useTranslationReady';

interface CountdownTimerProps {
  // Î∞©Î≤ï 1: Í≥ÑÏÇ∞Îêú ÏãúÍ∞ÑÏùÑ Î∞õÎäî Î∞©Ïãù (Í∏∞Ï°¥ VoteCountdownTimer Î∞©Ïãù)
  timeLeft?: {
    days: number;
    hours: number;
    minutes: number;
    seconds: number;
  } | null;
  
  // Î∞©Î≤ï 2: ÏãúÍ∞ÑÏùÑ ÏßÅÏ†ë Î∞õÏïÑÏÑú Í≥ÑÏÇ∞ÌïòÎäî Î∞©Ïãù (Í∏∞Ï°¥ CountdownTimer Î∞©Ïãù)
  startTime?: string | null;
  endTime?: string | null;
  
  // ÏÉÅÌÉú Í¥ÄÎ¶¨
  status?: 'scheduled' | 'in_progress' | 'ended' | 'ongoing' | 'upcoming' | 'completed';
  voteStatus?: 'upcoming' | 'ongoing' | 'completed'; // Ìò∏ÌôòÏÑ± Ïú†ÏßÄ
  
  // UI ÏòµÏÖò
  variant?: 'simple' | 'decorated'; // Í∏∞Î≥∏ Ïä§ÌÉÄÏùº vs ÌôîÎ†§Ìïú Ïä§ÌÉÄÏùº
  compact?: boolean;
  className?: string;
  showEmoji?: boolean;
}

export function CountdownTimer({ 
  timeLeft: propTimeLeft,
  startTime,
  endTime,
  status,
  voteStatus,
  variant = 'simple',
  compact = false,
  className = '',
  showEmoji = true
}: CountdownTimerProps) {
  const { t } = useLanguageStore();
  const isTranslationReady = useTranslationReady();
  
  const [calculatedTimeLeft, setCalculatedTimeLeft] = useState<{
    days: number;
    hours: number;
    minutes: number;
    seconds: number;
  }>({ days: 0, hours: 0, minutes: 0, seconds: 0 });

  // ÏÉÅÌÉú Ï†ïÍ∑úÌôî (voteStatusÏôÄ status Ìò∏ÌôòÏÑ± Ï≤òÎ¶¨)
  const normalizedStatus = (() => {
    if (voteStatus) {
      switch (voteStatus) {
        case 'upcoming': return 'scheduled';
        case 'ongoing': return 'in_progress';
        case 'completed': return 'ended';
        default: return voteStatus;
      }
    }
    return status || 'ended';
  })();

  // ÏãúÍ∞Ñ Í≥ÑÏÇ∞ Î°úÏßÅ (startTime/endTimeÏù¥ Ï†úÍ≥µÎêú Í≤ΩÏö∞)
  useEffect(() => {
    if (propTimeLeft !== undefined) return; // propÏúºÎ°ú ÏãúÍ∞ÑÏù¥ Ï†úÍ≥µÎêòÎ©¥ Í≥ÑÏÇ∞ÌïòÏßÄ ÏïäÏùå
    if (normalizedStatus === 'ended') return;

    const targetTime = normalizedStatus === 'scheduled' ? startTime : endTime;
    if (!targetTime) return;

    const updateTimer = () => {
      const now = new Date().getTime();
      const target = new Date(targetTime).getTime();
      const difference = target - now;

      if (difference > 0) {
        const days = Math.floor(difference / (1000 * 60 * 60 * 24));
        const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
        const seconds = Math.floor((difference % (1000 * 60)) / 1000);

        setCalculatedTimeLeft({ days, hours, minutes, seconds });
      } else {
        setCalculatedTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0 });
      }
    };

    updateTimer();
    const interval = setInterval(updateTimer, 1000);
    return () => clearInterval(interval);
  }, [startTime, endTime, normalizedStatus, propTimeLeft]);

  // ÏµúÏ¢Ö ÏÇ¨Ïö©Ìï† ÏãúÍ∞Ñ Í≤∞Ï†ï
  const timeLeft = propTimeLeft || calculatedTimeLeft;

  // ÏßÑÌñâ Ï§ëÏù¥ ÏïÑÎãàÍ±∞ÎÇò ÏãúÍ∞ÑÏù¥ ÏóÜÏúºÎ©¥ Ïà®ÍπÄ (VoteCountdownTimer Ìò∏ÌôòÏÑ±)
  if (voteStatus && voteStatus !== 'ongoing') return null;
  if (normalizedStatus === 'ended' && !propTimeLeft) {
    return (
      <div className={`text-center py-2 ${className}`}>
        <span className="text-sm font-medium text-gray-500">
          {isTranslationReady ? (t('text_vote_ended') || 'Ìà¨Ìëú Ï¢ÖÎ£å') : 'Ìà¨Ìëú Ï¢ÖÎ£å'}
        </span>
      </div>
    );
  }

  if (!timeLeft) return null;

  const { days, hours, minutes, seconds } = timeLeft;
  const isExpired = days === 0 && hours === 0 && minutes === 0 && seconds === 0;

  if (isExpired) {
    return (
      <div className={`flex items-center gap-2 ${className}`}>
        {showEmoji && <span className='text-xl'>üö´</span>}
        <span className={`font-bold text-red-600 ${compact ? 'text-sm' : 'text-sm md:text-base'}`}>
          {t('vote_status_closed')}
        </span>
      </div>
    );
  }

  const formatTime = (time: number) => time.toString().padStart(2, '0');

  const getCountdownText = () => {
    if (!isTranslationReady) {
      return normalizedStatus === 'scheduled' ? 'ÏãúÏûëÍπåÏßÄ' : 'Ï¢ÖÎ£åÍπåÏßÄ';
    }
    return normalizedStatus === 'scheduled' 
      ? (t('text_vote_countdown_start') || 'ÏãúÏûëÍπåÏßÄ')
      : (t('text_vote_countdown_end') || 'Ï¢ÖÎ£åÍπåÏßÄ');
  };

  // Simple variant (Í∏∞Ï°¥ CountdownTimer Ïä§ÌÉÄÏùº)
  if (variant === 'simple') {
    return (
      <div className={`text-center py-2 ${className}`}>
        <div className="text-xs text-gray-600 mb-1">
          {getCountdownText()}
        </div>
        <div className="flex justify-center items-center space-x-1 text-sm font-bold">
          <span className="bg-primary/10 text-primary px-2 py-1 rounded text-xs">
            {days} {t('time_unit_day')}
          </span>
          <span className="text-gray-400">:</span>
          <span className="bg-primary/10 text-primary px-2 py-1 rounded text-xs">
            {formatTime(hours)} {t('time_unit_hour')}
          </span>
          <span className="text-gray-400">:</span>
          <span className="bg-primary/10 text-primary px-2 py-1 rounded text-xs">
            {formatTime(minutes)} {t('time_unit_minute')}
          </span>
          <span className="text-gray-400">:</span>
          <span className="bg-primary/10 text-primary px-2 py-1 rounded text-xs">
            {formatTime(seconds)} {t('time_unit_second')}
          </span>
        </div>
      </div>
    );
  }

  // Decorated variant (Í∏∞Ï°¥ VoteCountdownTimer Ïä§ÌÉÄÏùº)
  return (
    <div className={`flex items-center gap-2 ${className}`}>
      {showEmoji && <span className='text-xl'>‚è±Ô∏è</span>}
      <div className={`flex items-center gap-1 font-mono font-bold ${compact ? 'gap-0.5' : 'gap-1'}`}>
        <span className={`bg-blue-100 text-blue-800 px-1.5 py-0.5 rounded min-w-[20px] text-center ${compact ? 'text-xs px-1 py-0.5' : 'text-xs'}`}>
          {String(days).padStart(2, '0')}
        </span>
        <span className={`text-blue-600 ${compact ? 'text-xs' : 'text-xs'}`}>
          {t('time_unit_day')}
        </span>
        <span className={`bg-blue-100 text-blue-800 px-1.5 py-0.5 rounded min-w-[20px] text-center ${compact ? 'text-xs px-1 py-0.5' : 'text-xs'}`}>
          {String(hours).padStart(2, '0')}
        </span>
        <span className={`text-blue-600 ${compact ? 'text-xs' : 'text-xs'}`}>
          {t('time_unit_hour')}
        </span>
        <span className={`bg-blue-100 text-blue-800 px-1.5 py-0.5 rounded min-w-[20px] text-center ${compact ? 'text-xs px-1 py-0.5' : 'text-xs'}`}>
          {String(minutes).padStart(2, '0')}
        </span>
        <span className={`text-blue-600 ${compact ? 'text-xs' : 'text-xs'}`}>
          {t('time_unit_minute')}
        </span>
        <span className={`bg-red-100 text-red-800 px-1.5 py-0.5 rounded animate-pulse min-w-[20px] text-center ${compact ? 'text-xs px-1 py-0.5' : 'text-xs'}`}>
          {String(seconds).padStart(2, '0')}
        </span>
        <span className={`text-red-600 ${compact ? 'text-xs' : 'text-xs'}`}>
          {t('time_unit_second')}
        </span>
      </div>
    </div>
  );
} 